<template>
	<div class="about">
		<header-component></header-component>
		<div class="hj">
			<img src="../assets/image/tp.jpg" />
		</div>
		<div class="txt">
			{{msg}}
			<span v-show="underLine">_</span>
		</div>
	</div>
</template>

<script>
	import headerComponent from '../components/homeHeader'
	export default {
		data() {
			return {
				msg: '',
				str: '利用Promise实现异步回调函数嵌套,谁说我除了帅一无是处?背景是我双笙女神的忧郁,music是我双笙女神的温柔~~~',
				underLine: true
			}
		},
		components:{
			headerComponent: headerComponent
		},
		created() {
			setTimeout(() => {
				this.timer();
			}, 1000);
		},
		methods: {
			timer() {
				//分割字符串
				var strArr = this.str.split('');
				//创建定时器函数
				var start = (time, index) => {
					setTimeout(() => {
						this.msg += strArr[index];
						if(this.msg == this.str) {
							this.underLine = false;
						}
					}, time)
				}
				//创建promise
				var timePromise = () => {
					return new Promise((resolve, reject) => {
						resolve();
					})
				}
				//循环执行promise
				for(var i = 0; i < strArr.length; i++) {
					timePromise().then(start(i * 350, i));
				}
			}
		}
	}
</script>

<style scoped>
	.about {
		width: 100%;
		height: 100vh;
		background: url(../assets/image/ss2.jpg) no-repeat;
		background-size: 100% 100%;
		color: #fff;
		font-size: 0.8rem;
	}
	
	.hj {
		position: fixed;
		top: 2rem;
		text-align: center;
		width: 100%;
	}
	
	.hj img {
		width: 150px;
		height: 150px;
		border-radius: 50%;
		animation: run 10s linear 0s infinite;
	}
	
	@keyframes run {
		from {
			-webkit-transform: rotateZ(0deg);
		}
		to {
			-webkit-transform: rotateZ(360deg);
		}
	}
	
	.txt {
		margin: 0 auto;
		padding-top: 10rem;
		width: 80%;
		text-align: center;
		overflow: hidden;
	}
	
	.txt span {
		animation: shan .8s linear 0s infinite;
	}
	
	@keyframes shan {
		from {
			opacity: 1;
		}
		to {
			opacity: 0;
		}
	}
</style>


